.minimalist-template {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #f5f5f5;
    height: 100vh;
    justify-content: center;
  }
  
  .profile-section {
    text-align: center;
  }
  
  .profile-img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-bottom: 20px;
  }
  
  .name {
    font-size: 2rem;
    font-weight: bold;
  }
  
  .short-title {
    font-size: 1.2rem;
    color: gray;
  }
  
  .social-links {
    display: flex;
    justify-content: center;
    margin-top: 30px;
  }
  
  .social-icon {
    font-size: 3rem;
    color: black;
    margin: 0 10px;
    transition: transform 0.3s;
  }
  
  .social-icon:hover {
    transform: scale(1.1);
    color: #007bff;
  }
    